import React from 'react';
import PageHeading from '../../components/Shared/PageHeading';
import PageMarkBook from '../../components/Shared/PageMarkBook';
import FakeTerminalUI from '../../components/Shared/FakeTerminalUI';
import { CopyBlock, CodeBlock, dracula, tomorrowNightBright } from 'react-code-blocks';

export default function HooksPage() {
	return (
		<div className='flex'>
			<div className='w-full md:px-5 pb-10'>
				<PageHeading text={'Hooks'} addOnClass={'text-left'} markedAs={'hooks'} />
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg'>
					Trong ReactJS, "Hooks" l cc hm c bit gip bn s dng cc tnh nng ca React trong cc Functional
					Components. Hooks gip bn s dng state v cc tnh nng ca lifecycle trong cc functional components, gip
					chng tr nn mnh m v linh hot hn. Cc hooks tr thnh mt cch tip cn chnh cho vic qun l state v
					cc kha cnh khc ca component. Di y l mt s state thng c dng trong ReactJS
				</p>
				<PageHeading text={'useState'} addOnClass={'text-left'} markedAs={'use-state'} />
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg'>
					<b>`useState()`</b> l mt hook c cung cp sn trong React, chng c s dng  gip user nh ngha v
					qun l state trong RFC.
					<br />  s dng c useEffect, cc bn c th lm theo cc thao tc sau:
				</p>
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg font-bold'>B1: Import Hook:</p>
				<CopyBlock
					text={`// import hook t th vin 'react'
import React, { useState } from 'react';`}
					language={'jsx'}
					showLineNumbers={true}
					theme={tomorrowNightBright}
				/>
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg font-bold'>
					B2: S dng hook trong Component:
				</p>
				<CopyBlock
					text={`// Khi to state mi c tn l "count"
const [count, setCount] = useState(0);`}
					language={'jsx'}
					showLineNumbers={true}
					theme={tomorrowNightBright}
				/>
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg '>
					Trong :
					<br /> <b className='pl-5 inline-block'>`count`</b>: Bin cha gi tr state hin.
					<br /> <b className='pl-5 inline-block'>`setCount`</b>: Hm s dng  cp nht gi tr <b>`count`.</b>
					<br /> <b className='pl-5 inline-block'>`1`</b>: Gi tr khi to cho state<b>`count`.</b>
				</p>
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg font-bold'>V d thc t:</p>
				<CopyBlock
					text={`import React, { useState } from 'react';
function Example() {
	const [count, setCount] = useState(0);

	return (
		<div>
			<p>You clicked {count} times</p>
			<button onClick={() => setCount(count + 1)}>
				Click me
			</button>
		</div>
	);
}`}
					language={'jsx'}
					showLineNumbers={true}
					theme={tomorrowNightBright}
				/>
				<div className='my-10'></div>
				<PageHeading text={'useEffect'} addOnClass={'text-left'} markedAs={'use-effect'} />
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg'>
					<b>`useEffect()`</b> l mt hook trong React c s dng  thc hin cc tc v khng ng b sau khi render
					mt component. N gip qun l cc side effects trong component functional, ging nh lifecycle methods trong
					component class. Hoc c s dng  chy cc block code theo mt danh sch iu kin - dependencies.
					<br />  s dng c useEffect, cc bn c th lm theo cc thao tc sau:
				</p>
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg font-bold'>B1: Import Hook:</p>
				<CopyBlock
					text={`// import hook t th vin 'react'
import React, { useEffect } from 'react';`}
					language={'jsx'}
					showLineNumbers={true}
					theme={tomorrowNightBright}
				/>
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg font-bold'>
					B2: S dng hook trong Component:
				</p>

				<CopyBlock
					text={`useEffect(() => {
	// Block 01
  // Cc tc v khng ng b sau khi component c render
  // ...

  // Cleanup function (nu cn)
  return () => {
    // Thc hin cng vic cleanup (nu c)
    // ...
  };
}, [dependencies]);
`}
					language={'jsx'}
					showLineNumbers={true}
					theme={tomorrowNightBright}
				/>
				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg '>
					Trong :
					<br /> <b className='pl-5 inline-block'>`Block 01`</b>: L blockcode s c thc thi ty theo iu kin ca{' '}
					<b>`dependencies`</b>
					<br /> <b className='pl-5 inline-block'>`Cleanup function`</b>: Function s chy khi <b>Component Unmount</b>{' '}
					hoc <b>Dependencies</b> thay i
					<br /> <b className='pl-5 inline-block'>`dependencies`</b>: L mt mng cha cc gi tr khin effect c
					kch hot li.
					<br /> Ty theo gi tr ca dependencies m useEffect s xc nh c cn thit phi thc thi <b>
						`Block 01`
					</b>{' '}
					hay khng. Ta c th phn chng ra thnh cc trng hp sau:
					<br /> <b className='pl-5 inline-block'>`[]`</b>: useEffect s thc thi 1 ln duy nht khi{' '}
					<b>`Component Mounted`;</b>
					<br /> <b className='pl-5 inline-block'>`[a,b,c]`</b>: useEffect s thc thi khi{' '}
					<b>bt k bin no trong mng `a,b,c` thay i;</b>
					<br /> <b className='pl-5 inline-block'>` `</b>: useEffect s thc thi khi{' '}
					<b>bt k khi no Component Update</b>
				</p>

				<p className='text-slate-900 dark:text-white my-5 leading-8 text-lg font-bold'>V d thc t:</p>
				<CopyBlock
					text={`import React, { useState, useEffect } from 'react';

const ExampleComponent = () => {
  const [count, setCount] = useState(0);
  const [otherCount, setOtherCount] = useState(0);

  useEffect(() => {
    // Gi mi khi component c render hoc count thay i
		// Khi otherCount thay i s khng khin effect c chy li
    console.log('Effect ran!');

    // Cleanup function (nu cn)
    return () => {
      console.log('Cleanup!');
    };
  }, [count]);

  return (
    <div>
      <p>Count: {count}</p>
      <button onClick={() => setCount(count + 1)}>
        Increment
      </button>
    </div>
		<div>
      <p>Other Count: {count}</p>
      <button onClick={() => setOtherCount(otherCount + 1)}>
        Increment
      </button>
    </div>
  );
};

export default ExampleComponent;
`}
					language={'jsx'}
					showLineNumbers={true}
					theme={tomorrowNightBright}
				/>
				<div className='my-10'></div>
				<p className='text-slate-900 italic dark:text-white my-5 leading-8 text-lg '>
					Note: Cn rt nhiu hooks khc nhng ti tm thi ch vit ti y, l do th ng nhin l li ri. Cc bn
					c th c thm v hooks ti
					<a
						href='https://react.dev/reference/react/hooks'
						target='_blank'
						rel='noreferrer'
						className='inline-block px-1 rounded text-slate-900 dark:text-white underline decoration-2 hover:text-cyan-500'
					>
						DOC
					</a>
				</p>
			</div>
			<PageMarkBook />
		</div>
	);
}
